<template>
    <div class="userlist">
        <div class="nav">
            <div class="headerimg" :class="{isonline:islogin}">
                <img v-if="$root.me!=null" :src="$root.me.headimg" alt="">
            </div>
            <div class="tlite"></div>
            <div class="headerimg"></div>
        </div>
        <div class="users">
            <div @click="chooseUser(item)" class="useritem" v v-for="(item,index) in firends" :key="index" >
                <div class="left"  :class="{isonline:item.isonline=='true',unread:unreadlist.indexOf(item.username)!=-1}">
                <img :src="item.headimg">
                </div>
                <div class="right">
                    <span class="username">{{item.username}}</span>
                    <span class="msg"></span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

    export default {
        name: "userlist",
        components:{
        },
        props:['islogin','users','chooseUser','unreadlist'],
        methods:{

        },
        computed:{
            firends:function () {
                if (this.$root.me==null){
                    return []
                }else {
                return  this.users.filter((item)=>{
                    return item.username!=this.$root.me.username;
                })
                }
            }
        },
    }
</script>

<style scoped>
    .unread{
    position: relative;
    }
    .unread::before{
        position: absolute;
        content: "";
        display: block;
        width:10px;
        height: 10px;
        border-radius: 5px;
        background: red;
        bottom: 5px;
        right: 5px;
    }
    .useritem .right{
        padding: 0 10px;
    }
    .useritem .left{
        filter: grayscale(1);
    }
    .useritem{
        display: flex;
        height: 80px;
        border-bottom: 1px solid #a6e1ec;
        align-items: center;
        padding: 0 10px;
        background:wheat;
    }
    .useritem .left img{
        width: 60px;
        height: 60px;
        border-radius:50%;
    }
.headerimg{
    height: 60px;
    width: 60px;
    filter:grayscale(1);
    margin: 0 10px;
}
.isonline{
    filter:grayscale(0)!important;
}
.nav{
    width: 100vw;
    height: 80px;
    background:skyblue;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.headerimg img{
    height: 60px;
    width: 60px;
    border-radius: 50%;
}
.tlite{
    font-size: 18px;
    font-weight: 900;
}
</style>