<template>
    <div class="chooseUser">
        <h1>选择角色</h1>
        <div class="list">
            <div class="item" @click="chooseEvent(item)" v-for="(item,index) in userlist" :key="index">
                <img :src="item.headimg">
                <span>{{item.username}}</span>
            </div>
        </div>
    </div>
</template>

<script>
    import socket from "../socket"
    export default {
        name: "chooseuser",
        props:['userlist'],
        methods:{
            chooseEvent:function (data) {
                this.$root.me=data;
                localStorage.chatme=JSON.stringify(data)
                socket.emit('login',data)
            }
        }
    }
</script>

<style scoped>
.chooseUser h1{
    font-size: 20px;
    text-align: center;
    padding:20px 0;
}
.chooseUser{
    width: 100vw;
    height: 100vh;
    background:yellow;
}
.list{
    display: flex;
    flex-wrap: wrap;
}
.item{
    width: 50%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    height: 130px;
    margin: 15px 0;
}
.item img{
    width: 100px;
    height: 100px;
    border-radius: 20px;
}
</style>