<template>
    <div class="chatuser">
        <div class="header">
            <span class="back" @click="Closechat">《</span>
            <div> {{touser.username}}</div>
        </div>
        <div class="chatlist" ref="chatbox">
        <div class="chatItem" v-for="(item,index) in chatlist" :key="index" :class="{self:$root.me.username==item.from.username}">
            <div class="header">
                <img :src="item.from.headimg">
            </div>
            <div class="chatContent">
                {{item.content}}
            </div>
        </div>
        </div>
        <div class="inputcom">
        <input type="text" v-model="inputdata" @keydown.enter="sendEvent">
            <button @click="sendEvent">发送</button>
        </div>
    </div>
</template>

<script>
import socket from "../socket"
    export default {
        name: "chatuser",
        props:['touser','Closechat','newsMsg'],
        data(){
          return{
              chatlist:[],
              inputdata:""
          }
        },
        components:{
        },
        methods:{
            sendEvent:function () {
                let msg={
                    from:this.$root.me,
                    to:this.touser,
                    content:this.inputdata,
                    time:new Date().getTime()
                }
                socket.emit('sendMsg',msg)
                this.chatlist.push(msg)
                this.saveStorage();
            },
            saveStorage(){
                let strkey='chat-user-'+this.$root.me.username+'-'+this.touser.username;
                localStorage[strkey]=JSON.stringify(this.chatlist);
            },
            getStorage(){
                let strkey='chat-user-'+this.$root.me.username+'-'+this.touser.username;
                localStorage[strkey]=localStorage[strkey]?localStorage[strkey]:'[]';
                this.chatlist=JSON.parse(localStorage[strkey]);
            },
            TOBottom(){
                let chatbox=this.$refs.chatbox;
                chatbox.scrollTop=chatbox.scrollHeight-chatbox.clientHeight;
            }

        },
        beforeMount() {
            this.getStorage()
            socket.emit('readMsg',{
                self:this.$root.me.username,
                username:this.touser.username,
            })
        },
        mounted() {
            this.TOBottom();
        },
        updated() {
            this.TOBottom();
        },
        watch: {
            newsMsg:function (val) {
                this.chatlist.push(val);
                this.saveStorage();
            }
        }
    }
</script>

<style scoped>
.chatuser{
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 0;
    left: 0;
    background:#a6e1ec;
}
.chatuser .header{
    font-size: 18px;
    font-weight: 900;
    background: skyblue;
    height: 40px;
    margin: 0;
    padding: 0;
    text-align: center;
    line-height: 40px;
}
    .chatlist{
        flex: 1;
        overflow: scroll;
    }
    .inputcom{
        height: 50px;
        display: flex;
        background: white;
        justify-content: space-between;
        border: 1px solid #b2dba1;
    }
    .inputcom input{
        margin: 3px 6px;
        width: 270px;
        height: 40px;
        border-radius: 5px;
        outline: none;

    }
    .inputcom button{
        margin: 3px;
        width: 80px;
        height: 40px;
        border-radius: 5px;
        outline: none;
        border: 1px solid #b2dba1;
          }
    .chatuser>.header{
        position: relative;
    }
    .chatuser .back{
        display:block;
        width: 40px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        position: absolute;
        left: 0;
        top:0;
    }
    .chatItem{
        display: flex;
        margin: 5px 10px;
    }
    .chatItem .header img{
        width: 50px;
        height: 50px;
        border-radius: 50%;

    }
    .chatItem .chatContent{
        background: #8c8c8c;
        border-radius: 5px;
        padding: 6px 10px;
        margin: 5px 5px 10px 10px;
        line-height: 34px;
        position: relative;
    }
.chatItem .chatContent::before{
    content:"";
    position: absolute;
    width: 0;
    height: 0;
    border-right: 10px solid transparent;
    border-top: 4px  solid transparent;
    border-bottom: 2px solid transparent;
    top: 19px;
    left: 33px;

}
.chatItem .self .chatContent::before{
    display: block;
    content:"";
    position: absolute;
    width: 0;
    height: 0;
    border-left: 10px solid #8c8c8c;
    border-top: 10px  solid transparent;
    border-bottom: 5px solid transparent;
    top: 20px;
    right: -10px;
    left: initial;
    background: blue;
}
    .chatItem.self{
        flex-direction: row-reverse;
        justify-content:flex-start;
    }

.chatItem .self .chatContent{
    margin: 0px 20px 0px 0px;
}
</style>