<template>
  <div id="app">
    <chooseuser v-if="$root.me==null" :userlist="userlist">
    </chooseuser>
    <userlist :islogin="islogin" :users="users" :chooseUser="chooseUser" :unreadlist="unreadlist"></userlist>
    <chatuser v-if="ischat" :touser="touser" :Closechat="Closechat" :newsMsg='newsMsg'></chatuser>
  </div>
</template>

<script>
import chooseuser from "../src/components/chooseuser"
import userlist from "../src/components/userlist"
import socket from "./socket"
import chatuser from "../src/components/chatuser"
import axios from "axios"
export default {
  name: 'App',
  components: {
    chooseuser,
    userlist,
    chatuser
  },
  data(){
    return{
      userlist:[],
      islogin:false,
      users:[],
      ischat:false,
      touser:null,
      unreadlist:[],
      newsMsg:null

    }
  },
  async beforeMount() {
    let res = await axios.get('http://localhost:3000/api/userlist')
        console.log(res)
       this.userlist=res.data
  },
  mounted() {
    socket.on('login',(data)=> {
      if (data.state=='ok'){
        this.islogin=true;
        socket.emit('users')
      }
    })
    socket.on('logout',(data)=>{
      console.log(data)
      this.islogin=false;
      socket.disconnect();
    })
    socket.on('disconnect',()=>{
      console.log("断开链接")
    })
    socket.on('users',(data)=>{
      console.log(data);
      this.users=data;
    })
    socket.on('unread',(data)=>{
      /*console.log(data)
      console.log("unread");*/
      data.forEach((item,index)=>{
        item.from=this.userOBJ[item.from];
        item.to=this.userOBJ[item.to];
          this.unreadlist.push(item.from);
          console.log(index);
          let strkey='chat-user-'+this.$root.me.username+'-'+item.from.username;
           localStorage[strkey]=localStorage[strkey]?localStorage[strkey]:'[]';
          let newArr=JSON.parse(localStorage[strkey]);
          newArr.push(item);
         localStorage[strkey]=JSON.stringify(newArr);
        /* console.log(this.userOBJ)*/
      })
    })
     socket.on('accept',(msg)=> {
     console.log(msg)
      console.log("接受数据")
     if((this.ischat==true && msg.from.username==this.touser.username)||(msg.to.username==this.touser.username && msg.to.isgroup=='true')){
      this.newsMsg=msg;
     }else {
       let strKey = 'chat-user-'+this.$root.me.username+'-'+msg.from.username;
       localStorage[strKey]=localStorage[strKey]?localStorage[strKey]:'[]';
       let newArr=JSON.parse(localStorage[strKey]);
       newArr.push(msg)
       localStorage[strKey]=JSON.stringify(newArr);
       this.unreadlist.push(msg.from.username);
     }
    })
  },
  methods:{
    chooseUser:function (user) {
      this.touser=user;
      this.ischat=true;
     let index =  this.unreadlist.indexOf(user.username);
   this.unreadlist.splice(index,1);
    },
    Closechat:function () {
      this.ischat=false;
    }
  },
  computed:{
    userOBJ:function () {
      let  obj={}
      this.users.forEach((item,index)=>{
        obj[item.username]=item;
        console.log(index)
      })
      return obj;
    }
  },
}
</script>

<style scoped>
*{
  margin: 0;
  padding: 0;
}
</style>
